button.efl-button {
  height: $efl-button-height;
  line-height: $efl-button-height;
  font-size: $efl-button-font-size;
  padding: 0 $efl-button-padding;
  border-radius: 1000px;
}

button.efl-button-small {
  height: $efl-button-small-height;
  line-height: $efl-button-small-height;
  font-size: $efl-button-small-font-size;
  padding: 0 $efl-button-small-padding;
  border-radius: 1000px;
}

button.efl-primary {
  @include button-variant($mui-btn-primary-font-color, $efl-primary-color);

  > .mui-ripple-effect {
    background-color: #FFF;
  }
}

button.efl-action {
  @include button-variant($mui-btn-primary-font-color, $efl-action-color);

  > .mui-ripple-effect {
    background-color: #FFF;
  }
}


button.efl-disabled {
  @include button-variant($mui-btn-primary-font-color, $efl-disabled-color);

  &:hover,
  &:focus,
  &:active {
    background-color: darken($efl-disabled-color, 5%);
  }

  background-color: $efl-disabled-color;
  opacity: 1;
}

button[disabled] {
  background-color: $efl-disabled-color;
  opacity: 1;
}
