// colors
$efl-primary-color: #00AFF0 !default;
$efl-action-color: #66CC33 !default;
$efl-disabled-color: #DDE2E8 !default;

// input
$efl-input-padding: 20px !default;
$efl-input-height: 44px !default;
$efl-input-border-color: #D1D3D4 !default;

// labels
$efl-label-color: #A1A1A0 !default;
$efl-label-font-size: 14px !default;

// buttons
$efl-button-height: 48px !default;
$efl-button-font-size: 22px !default;
$efl-button-padding: 23px !default;

$efl-button-small-height: $efl-button-height * 0.8 !default;
$efl-button-small-font-size: $efl-button-font-size * 0.8 !default;
$efl-button-small-padding: $efl-button-padding * 0.8 !default;

// radio buttons
$efl-radio-size: 26px !default;
$efl-radio-border-width: 2px !default;
$efl-radio-inside-border: 3px !default;
$efl-radio-dot-margin: 20px !default;
$efl-radio-background-color: #F0F3F7 !default;
$efl-radio-vertical-padding: 8px !default;
$efl-radio-label-width: 300px !default;
$efl-radio-label-padding: $efl-radio-dot-margin !default;
$efl-radio-elements-margin: 10px !default;