.efl-radio {
  @extend .mui-radio;

  $efl-radio-space: $efl-radio-size + 2 * $efl-radio-dot-margin;

  position: relative;
  width: $efl-radio-label-width;
  background-color: $efl-radio-background-color;
  margin-top: 0;
  margin-bottom: 0;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-content: stretch;

  > label {
    flex-shrink: 2;
    flex-grow: 1;
    padding: $efl-radio-vertical-padding $efl-radio-label-padding $efl-radio-vertical-padding 0;
    text-align: left;
  }

  +.efl-radio {
    margin-top: $efl-radio-elements-margin;
  }

  input[type=radio]:not(old) {
    width: $efl-radio-space;
    margin: 0;
    padding: 0;
    opacity: 0;
    position: absolute;
    height: 100%;

    + span {
      min-width: $efl-radio-size;
      max-width: $efl-radio-size;
      height: $efl-radio-size;
      margin: $efl-radio-vertical-padding $efl-radio-dot-margin;
      border: $efl-radio-border-width solid $efl-primary-color;
      border-radius: $efl-radio-size / 2;
      background: #FFFFFF;
      vertical-align: middle;
    }

    &:checked +span > span {
      $efl-radio-inside-dot: $efl-radio-size - 2 * ($efl-radio-border-width + $efl-radio-inside-border);
      display: block;
      width: $efl-radio-inside-dot;
      height: $efl-radio-inside-dot;
      border-radius: $efl-radio-inside-dot / 2;
      background: $efl-primary-color;
      margin-left: $efl-radio-inside-border;
      margin-top: $efl-radio-inside-border;
    }

    &[disabled] + span {
      border-color: $efl-disabled-color;
    }

    &[disabled]:checked + span > span {
      background-color: $efl-disabled-color;
    }
  }
}
